@import url(https://fonts.googleapis.com/css2?family=Montserrat&family=Roboto:wght@300&display=swap);
:root {
  --primary-color: white;
  --secondary-color: #31393c;
  --bottom-bar-height: 8vmax;
  --menu-font-color: #ffffff80;
  --heading-color: black;
  --text-color: black;
}

body {
  margin: 0;
  padding: 0px 0 0 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: white !important;
  background-color: var(--primary-color) !important;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  -ms-overflow-style: none;
}

::-webkit-scrollbar {
  display: none;
}

.main-area {
  padding: 70px 0px 30px 0px !important;
}

.clickable {
  cursor: pointer;
}

.center {
  display: -webkit-flex;
  display: flex;
  margin-top: 10%;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  height: 200px;
  -webkit-flex-direction: column;
          flex-direction: column;
}

h4 {
  font-family: "Montserrat";
  color: white;
  color: var(--primary-color);
}
a {
  font-family: "Montserrat";
}
h3 {
  font-family: "Roboto", sans-serif;
}
@media only screen and (max-width: 1350px) {
  .main-area {
    padding: 0px 0px 70px 0px !important;
    -webkit-align-items: center;
            align-items: center;
    z-index: 1;
  }
}

.nav-container {
  position: fixed !important;
  width: 100%;
  z-index: 3;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-align-content: center;
          align-content: center;
  box-shadow: black;
  background-color: var(--secondary-color);
  height: 6.5vh;
}
.nav-collapse {
  -webkit-justify-content: center;
          justify-content: center;
}
.nav-focus {
  position: fixed;
  top: 0;
  height: 100vh;
  width: 100vw;
  opacity: 0.2;
  z-index: 2 !important;
  background-color: var(--secondary-color);
}
.navbar-toggler {
  border: 0px !important;
}
.nav-logo {
  height: 40px;
}
.nav-item {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
}
.nav-menu a {
  border-radius: 5px;
}
.nav-menu li {
  padding: 10px;
}
.nav-menu strong {
  margin: 0 10px 0 10px;
}

#submission-box {
  display: grid;
  grid-template-columns: 1fr 1fr;
  padding: 0px 35px 35px 35px;
  grid-column-gap: 5px;
  -webkit-column-gap: 5px;
          column-gap: 5px;
  grid-template-areas:
    "close-form close-form"
    "first last"
    "number email"
    "rooms hallways";

  font-size: 16px !important;
  text-align: left;
}
#close-form {
  grid-area: close-form;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
}
#close-form button {
  box-shadow: none !important;
  color: var(--primary-color);
}
#first {
  grid-area: first;
}
#last {
  grid-area: last;
}
#number {
  grid-area: number;
}
#email {
  grid-area: email;
}
#rooms {
  grid-area: rooms;
}
#hallways {
  grid-area: hallways;
}
.form-submit-btn {
  margin-bottom: 20px;
}
.form-error {
  padding: 6px;
  border-radius: 5px;
}

@media only screen and (max-width: 1350px) {
  .nav-container {
    position: fixed !important;
    bottom: 0 !important;
    height: var(--bottom-bar-height);
    width: 100vw;
  }
  .hidden {
    display: hidden;
  }
  .nav-collapse {
    position: fixed !important;
    bottom: var(--bottom-bar-height) !important;
    background-color: var(--secondary-color);
    width: 100vw;
    border-radius: 10px 10px 0 0;
    text-align: center;
  }
  .nav-menu {
    padding-top: 15px;
    padding-bottom: 15px;
  }
  .nav-menu strong {
    font-size: 18px;
  }
  .nav-menu a {
    width: 90%;
  }
  #submissionPopup {
    position: fixed !important;
    bottom: var(--bottom-bar-height) !important;
    background-color: var(--secondary-color);
    width: 100vw;
    border-radius: 10px 10px 0 0;
    text-align: center;
  }
  #submissionPopup h2 {
    margin: 3vh auto -2vh auto;
    color: var(--muted-heading);
    font-size: 90%;
  }
  #submissionPopup label {
    display: none !important;
  }
  .popup-content {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: center;
            justify-content: center;
    margin: 20px;
  }
  .popup-content strong {
    font-size: 125%;
    margin: 2vh auto 2vh auto;
  }
  .popup-content button {
    padding: 15px 30px 5px 30px !important;
    margin: 0 5vw 0 5vw;
    background-color: var(--secondary-color);
    color: var(--primary-color);
  }
}

/*Used for two columns in the awkward mobile transition phase*/
@media only screen and (min-width: 650px) and (max-width: 1200px) {
  .nav-menu {
    display: grid;
    grid-template-columns: 1fr 1fr;
    padding: 25px;
    grid-column-gap: 5px;
    -webkit-column-gap: 5px;
            column-gap: 5px;
    font-size: 16px !important;
    color: white;
  }
}

/*Scales down the padding to fit text on smaller screens*/
@media only screen and (max-width: 450px) {
  #submission-box {
    padding: 20px;
  }
}

@media only screen and (min-width: 1350px) {
  .form-group label {
    color: white;
  }
  #submissionPopup {
    position: fixed !important;
    top: 40vh;
    margin: auto;
    background-color: var(--secondary-color);
    width: 35vw;
    border-radius: 5px;
    text-align: center;
  }
  #submissionPopup h2 {
    margin: 3vh auto 1vh auto;
    color: var(--muted-heading);
    font-size: 90%;
  }
  #submission-box ::-webkit-input-placeholder {
    color: transparent;
  }
  #submission-box :-ms-input-placeholder {
    color: transparent;
  }
  #submission-box ::placeholder {
    color: transparent;
  }
  .popup-content {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: center;
            justify-content: center;
    padding: 15px;
    margin: 20px;
  }
  .popup-content strong {
    font-size: 130%;
  }
  .popup-content button {
    margin: 0 2vw 0 2vw;
    background-color: var(--secondary-color);
    color: var(--primary-color);
  }
}

.home-container {
  display: grid;
  padding: 5vw;
  justify-items: center;
  -webkit-align-items: center;
          align-items: center;
  grid-template-columns: 1fr 1fr;
}

.text-container {
  text-align: center;
  font-size: 25px;
  padding: 30px;
}
.text-container h1 {
  color: var(--heading-color);
  font-family: "Montserrat";
  margin-bottom: 15px;
  text-align: center;
}
.text-container p {
  color: var(--text-color);
  text-align: justify;
}

.image {
  padding: 5px;
  border-radius: 20px;
  border: 2px solid black;
}

@media only screen and (max-width: 1350px) {
  /* Home */
  .home-container {
    grid-template-columns: 1fr;
  }
  .text-container p {
    font-size: 18px;
  }
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

